export default {
    data() {
        return {
            agreement: '',
            chnerot: '',
            flag: false,
            fllaag: false
        };
    },
    onShow() {
        var that = this;
        uni.request({
            url: this.url + 'usermachine/agreement/',
            method: 'GET',
            header: {
                Authorization: 'JWT' + ' ' + this.global_.token
            },
            success(res) {
                that.chnerot = res.data.data.user_agreement;
                that.agreement = res.data.data.agreement;
                if (that.chnerot == 0) {
                    that.flag = true;
                }
            }
        });
    },
    methods: {
        sss: function() {
            var that = this;
            uni.request({
                url: this.url + 'usermachine/agreement/',
                method: 'POST',
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success(res) {
                    if (res.statusCode == 200) {
                        that.fllaag = true;
                        that.flag = false;
                        uni.showToast({
                            title: '已阅读并同意协议'
                        });
                    }
                }
            });
        },
        aaa: function() {
            const downloadTask = uni.downloadFile({
                url: this.url + 'media/用户服务协议.pdf',
                success: res => {
                    if (res.statusCode === 200) {
                        uni.showToast({
                            title: '下载成功',
                            duration: 3000
                        });
                    }
                }
            });
        }
    }
};
